    },
    computed: {
      currentTabData() {
        return this.tabs.find(tab => tab.label === this.selectedTab);
      },
      calculatedCosts() {
        return this.tabs.reduce((acc, tab) => {
          acc[tab.label] = this.squareMeters * tab.rate;
          return acc;
        }, {});
      },
      // tabCosts() {
      //   const result = {};
      //   this.tabs.forEach(tab => {
