      //     result[tab.label] = this.squareMeters * tab.rate;
      //     // }
      //   });
      //   return result;
      // },
      tabCosts() {
        const result = {};
        this.tabs.forEach(tab => {
          result[tab.label] = this.squareMeters * this.getCurrentRate(tab);
        });
        return result;
      }
    },
    methods: {
      selectTab(tab) {
        this.selectedTab = tab;
      },
      getCurrentRate(tab) {
        if (tab.label === ' ') {
          return this.squareMeters <= 100 ? 65 : 52;
        }
        if (tab.label === '  ') {
          return this.squareMeters <= 150 ? 180 : 168;
        }
        if (tab.label === ' ') {
          const sqm = this.squareMeters;
          if (this.isAfter) {
            return this.isStandard ? (sqm >= 100 ? 50 : 80) : 90;
          } else {
            return this.isStandard ? (sqm >= 100 ? 50 : 60) : 80;
          }
        }
        return tab.rate;
      },

      selectTabByIndex(index) {
        this.selectedTabIndex = index;
      },
      handleResize() {
        this.isMobile = window.innerWidth <= 768;
      },
      getTabProps(tab) {
        if (tab.label === ' ') {
          return {};
        }
        return {
          baseRate: tab.rate,
          tasks: tab.tasks,
          services: tab.services
        };
      },

    },
    mounted() {
      this.handleResize();
      window.addEventListener('resize', this.handleResize);
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.handleResize);
    }

  };
